.photo-row {
  img {
    width: 75px;
    height: 75px;
  }
}

.radio-butt {
  display: inline-flex;
  align-items: center;
  margin-right: 1rem;
  cursor: pointer;

  input[type="radio"] {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    width: 16px;
    height: 16px;
    border: 2px solid var(--app-color-secondary);
    border-radius: 50%;
    margin-right: 0.5rem;
    position: relative;
    cursor: pointer;
    outline: none;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(0);
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: var(--app-color-secondary);
      transition: transform 0.2s ease-in-out;
    }

    &:checked::before {
      transform: translate(-50%, -50%) scale(1);
    }

    &:focus-visible {
      box-shadow: 0 0 0 3px rgba(var(--secondary-color-rgb), 0.3);
    }
  }

  span {
    color: var(--app-color-primary);
    font-size: 0.875rem;
    padding-left: 7px;
  }

  &:hover {
    input[type="radio"] {
      border-color: var(--app-color-dark);

      &::before {
        background-color: var(--app-color-dark);
      }
    }
  }
}
